/* global document, Office, Word */

import CVSS31 from "./cvss31";

const metricOrder = [
  "AV",
  "AC",
  "PR",
  "UI",
  "S",
  "C",
  "I",
  "A",
  "E",
  "RL",
  "RC",
  "CR",
  "IR",
  "AR",
  "MAV",
  "MAC",
  "MPR",
  "MUI",
  "MS",
  "MC",
  "MI",
  "MA",
];

const optionalMetrics = new Set(metricOrder.slice(8));

Office.onReady((info) => {
  if (info.host === Office.HostType.Word) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
  }
  initializeForm();
});

function initializeForm() {
  const form = document.getElementById("cvss-form");
  const resetButton = document.getElementById("reset-form");
  const loadButton = document.getElementById("load-selection");
  const updateButton = document.getElementById("update-selection");
  const insertButton = document.getElementById("insert-results");

  if (!form) {
    return;
  }

  form.addEventListener("submit", (event) => {
    event.preventDefault();
    handleCalculate(form);
  });

  if (loadButton) {
    loadButton.addEventListener("click", () => {
      loadFromSelection(form);
    });
  }

  if (updateButton) {
    updateButton.addEventListener("click", () => {
      updateSelectionFromForm(form);
    });
  }

  if (insertButton) {
    insertButton.addEventListener("click", () => {
      insertResultsIntoDocument(form);
    });
  }

  if (resetButton) {
    resetButton.addEventListener("click", () => {
      form.reset();
      clearResults();
    });
  }

  clearResults();
}

function handleCalculate(form) {
  const errorMessage = document.getElementById("error-message");
  const calculation = calculateMetricsFromForm(form);

  if (!calculation.success) {
    renderError(errorMessage, calculation.error);
    clearScoreFields();
    return calculation;
  }

  renderError(errorMessage, null);

  updateResults(calculation.result, calculation.xmlString);
  return calculation;
}

function getMetricValues(form) {
  const formData = new FormData(form);
  const values = {};

  metricOrder.forEach((metric) => {
    const fieldValue = formData.get(metric);
    values[metric] = fieldValue && fieldValue !== "" ? fieldValue : "X";
  });

  return values;
}

function renderError(container, result) {
  if (!container) {
    return;
  }

  if (!result) {
    container.textContent = "";
    return;
  }

  if (typeof result === "string") {
    container.textContent = result;
    return;
  }

  const metricList = Array.isArray(result.errorMetrics) ? ` (${result.errorMetrics.join(", ")})` : "";
  container.textContent = `${result.errorType}${metricList}`;
}

function updateResults(result, xmlString) {
  setText("base-score", result.baseMetricScore);
  setText("base-severity", formatSeverity(result.baseSeverity));
  setText("temporal-score", result.temporalMetricScore);
  setText("temporal-severity", formatSeverity(result.temporalSeverity));
  setText("environmental-score", result.environmentalMetricScore);
  setText("environmental-severity", formatSeverity(result.environmentalSeverity));
  setText("vector-string", result.vectorString);
  setTextArea("xml-output", xmlString);
}

function applyMetricsToForm(form, metrics) {
  metricOrder.forEach((metric) => {
    const field = form.elements.namedItem(metric);
    if (!field) {
      return;
    }

    const value = metrics.hasOwnProperty(metric) ? metrics[metric] : undefined;

    if (typeof value === "string" && value !== "") {
      field.value = value;
    } else if (optionalMetrics.has(metric)) {
      field.value = "X";
    }
  });
}

function clearResults() {
  clearScoreFields();
  setText("vector-string", "-");
  setTextArea("xml-output", "");
  const errorMessage = document.getElementById("error-message");
  if (errorMessage) {
    errorMessage.textContent = "";
  }
}

function clearScoreFields() {
  setText("base-score", "-");
  setText("base-severity", "");
  setText("temporal-score", "-");
  setText("temporal-severity", "");
  setText("environmental-score", "-");
  setText("environmental-severity", "");
}

function setText(id, value) {
  const element = document.getElementById(id);
  if (element) {
    element.textContent = value;
  }
}

function setTextArea(id, value) {
  const element = document.getElementById(id);
  if (element) {
    element.value = value;
  }
}

function formatSeverity(severity) {
  return severity ? `(${severity})` : "";
}

async function loadFromSelection(form) {
  const errorMessage = document.getElementById("error-message");

  if (Office.context.host !== Office.HostType.Word) {
    renderError(errorMessage, "Loading from the document is only available in Word.");
    return;
  }

  try {
    const selectionText = await Word.run(async (context) => {
      const selection = context.document.getSelection();
      selection.load("text");
      await context.sync();
      return selection.text || "";
    });

    const candidate = selectionText.trim();

    if (!candidate) {
      renderError(errorMessage, "Please select a CVSS v3.1 vector string in the document.");
      return;
    }

    const normalized = candidate.replace(/\s+/g, "");

    if (!CVSS31.vectorStringRegex_31.test(normalized)) {
      renderError(errorMessage, "Selection does not contain a valid CVSS v3.1 vector string.");
      return;
    }

    const metrics = extractMetricsFromVector(normalized);

    if (!metrics) {
      renderError(errorMessage, "Unable to parse the selected vector string.");
      return;
    }

    applyMetricsToForm(form, metrics);
    renderError(errorMessage, null);
    handleCalculate(form);
  } catch (error) {
    renderError(errorMessage, "Unable to read selection. Try again.");
    console.error("Failed to load CVSS vector from selection:", error);
  }
}

function extractMetricsFromVector(vectorString) {
  if (!CVSS31.vectorStringRegex_31.test(vectorString)) {
    return null;
  }

  const fragments = vectorString.substring(CVSS31.CVSSVersionIdentifier.length).split("/");
  const metrics = {};

  fragments.forEach((fragment) => {
    if (!fragment) {
      return;
    }

    const [metric, value] = fragment.split(":");
    if (metric && value) {
      metrics[metric] = value;
    }
  });

  return metrics;
}

function calculateMetricsFromForm(form) {
  const values = getMetricValues(form);

  const result = CVSS31.calculateCVSSFromMetrics(
    values.AV,
    values.AC,
    values.PR,
    values.UI,
    values.S,
    values.C,
    values.I,
    values.A,
    values.E,
    values.RL,
    values.RC,
    values.CR,
    values.IR,
    values.AR,
    values.MAV,
    values.MAC,
    values.MPR,
    values.MUI,
    values.MS,
    values.MC,
    values.MI,
    values.MA
  );

  if (!result.success) {
    return { success: false, error: result, values };
  }

  const xmlResult = CVSS31.generateXMLFromMetrics(
    values.AV,
    values.AC,
    values.PR,
    values.UI,
    values.S,
    values.C,
    values.I,
    values.A,
    values.E,
    values.RL,
    values.RC,
    values.CR,
    values.IR,
    values.AR,
    values.MAV,
    values.MAC,
    values.MPR,
    values.MUI,
    values.MS,
    values.MC,
    values.MI,
    values.MA
  );

  if (!xmlResult.success) {
    return { success: false, error: xmlResult, values, result };
  }

  return {
    success: true,
    values,
    result,
    xmlString: xmlResult.xmlString,
  };
}

async function updateSelectionFromForm(form) {
  const errorMessage = document.getElementById("error-message");

  if (Office.context.host !== Office.HostType.Word) {
    renderError(errorMessage, "Updating the document selection is only available in Word.");
    return;
  }

  const calculation = handleCalculate(form);
  if (!calculation || !calculation.success) {
    return;
  }

  try {
    const vectorString = calculation.result.vectorString;
    await Word.run(async (context) => {
      const selection = context.document.getSelection();
      selection.insertText(vectorString, Word.InsertLocation.replace);
      await context.sync();
    });
  } catch (error) {
    renderError(errorMessage, "Unable to update selection. Try again.");
    console.error("Failed to update selection with CVSS vector:", error);
  }
}

async function insertResultsIntoDocument(form) {
  const errorMessage = document.getElementById("error-message");

  if (Office.context.host !== Office.HostType.Word) {
    renderError(errorMessage, "Inserting results is only available in Word.");
    return;
  }

  const calculation = handleCalculate(form);
  if (!calculation || !calculation.success) {
    return;
  }

  try {
    await Word.run(async (context) => {
      const selection = context.document.getSelection();
      selection.insertText(calculation.result.vectorString, Word.InsertLocation.replace);
      await context.sync();
    });
    renderError(errorMessage, null);
  } catch (error) {
    renderError(errorMessage, "Unable to insert results. Try again.");
    console.error("Failed to insert CVSS results into document:", error);
  }
}
